  width: 100%;
  height: 100%;
  padding: 15px;
  position: relative;
  /* 地图dom，分屏 */
  .viewDoms{
    width:100%;height:100%;overflow: hidden;
    .splitPane{
      height: 100%;
      #referViewDiv,#viewDiv{
        width: 100%;height: 100%;
      }
    }
  }
  .btns {
    width: 100%;
    text-align: center;
    position: absolute;
    right: 20px;
    bottom: 30px;
    .ivu-btn {
      border-radius: 0;
      i {
        margin-right: 5px;
      }
      &.btn-save {
        background-color: #4aa432;
        border-color: #4aa432;
        &:hover, &:focus {
          background-color: #388524;
          border-color: #388524;
        }
      }
      &.btn-commit {
        background-color: #597fd5;
        border-color: #597fd5;
        &:hover, &:focus {
          background-color: #486ec4;
          border-color: #486ec4;
        }
      }
      &.btn-cancel {
        background-color: #ff3b3b;
        border-color: #ff3b3b;
        &:hover, &:focus {
          background-color: #e22727;
          border-color: #e22727;
        }
      }
    }
  }
  .mr15 {
    margin-right: 15px;
  }
  #mapLayers {
    .ivu-btn {
      padding: 0px 5px 0px;
    }
    .icon-tuceng {
      font-size: 20px;
    }
  }
  .pop-content {
    text-align: left;
    li {
      line-height: 35px;
      border-top: 1px solid #ccc;
      &:first-child {
        border: none;
      }
      i {
        font-size: 16px;
        margin-left: 30px;
      }
      .fraggableBox {
        &>p {
          padding-left: 22px;
          line-height: 20px;
        }
      }
    }
  }
  /* 工具条样式 */
  #toolbar, #orcharToolbar, #referBtn{
    box-shadow: none;
    .tools{
      background: #fff;
      margin-right: 5px;
      padding: 5px;
      float: right;
      border-radius: 9px;
      button {
        width: 30px;
        height: 30px;
        border: 0;
        background: #fff;
        cursor: pointer;
        outline: none;
        i{
          font-size: 16px;
        }
      }
      .active{
        background-color: #4C4C4C;
        color: #fff;
      }
    }
  }
  /* 操作面板基础样式 */
  .panel{
    margin-right: 5px;
    padding: 10px 10px 0;
    background: #fff;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
    width: 450px;
    overflow: auto;
    position: relative;
  }
  /* 属性选择框列表 */
  .attributeList{
    height: 150px;overflow: auto;border: 1px solid #d1d1d1;padding: 3px;
    ul{
      li{
        cursor:pointer;border: 1px solid #d1d1d1;padding: 3px;margin-bottom: 5px;border-radius: 5px;
        .iconfont{
          float: right;margin-right: 5px;font-size: 12px;line-height: 21px;
        }
        .unActive{
          color:#d1d1d1;
          cursor: not-allowed;
        }
      }
      .attrLiActive{
        background: #e0e0e0;
      }
      .attrCopyActive{
        border: 1px #960AC4 solid;
      }
    }
  }
  /* 属性选择框详情 */
  .attributeDetail{
    border-top: 1px #d1d1d1 solid;
    margin-top: 5px;
    height: 315px;
    overflow: auto;
    /* 属性面板表格 */
    .tab{
      background: #d1d1d1;
      width: 100%;
      font-size: 12px;
      th{
        background: #fafafa;
        padding: 1px;
        font-weight: normal;
      }
      td{
        background: #fff;
        padding: 3px;
        text-align: center;
      }
    }
  }
  /* 交集面板 */
  #intersectModal{
    ul{
      li{
        cursor: pointer;
        border: 1px solid #d1d1d1;
        text-align: center;
        padding: 3px 0;
        margin-bottom: 10px;
      }
      .panelActive{
        color: #fff;
        background: #4c4c4c;
      }
    }
  }
  /* 图层控制面板 */
  .layerControl{
    box-shadow: none;
    .annoBtn{
      width: 32px;height: 32px;border: 0;background: #fff;margin-bottom: 5px;cursor: pointer;
    }
    .layerPanel{
      padding: 10px;
      background: #fff;
      box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
      width: 300px;
      position: relative;
      ul{
        li{
          margin-bottom: 5px;
          padding: 3px;
        }
        .backLayer{
          cursor: pointer;position: relative;
          .backListPanel{
            padding: 5px;background: #fff;bottom:-5px;
            ul{
              li{
                width: 200px;cursor: pointer;
                &:last-child{
                  margin-bottom: 0;
                }
              }
            }
          }
        }
      }
    }
  }
  /* 左上角按钮样式 */
  .btnTools{
    width:32px;height:32px;border-radius: 9px;border: 0;cursor: pointer;outline: none;background: #fff;
  }
  /* 审核面板 */
  #annoPanel{
    padding-bottom: 5px;
    .annoList{
      height: 500px;overflow: auto;padding: 8px 5px;
      ul{
        border-top: 1px solid #d1d1d1;
        li{
          cursor:pointer;border-bottom: 1px solid #d1d1d1;padding: 3px;
          .point{
            display: inline-block;border-radius: 50%;height: 6px;width: 6px;margin: -6px 5px 0;
          }
          p{
            overflow:hidden;text-overflow:ellipsis;white-space:nowrap;padding: 0 0 0 20px;margin-top: -18px;width: calc(100% - 50px);
          }
          .iconfont{
            float: right;margin-top:-22px;
            .icon{
              margin-right: 5px;font-size: 18px;line-height: 21px;font-style: normal;
            }
          }
          .unActive{
            color:#d1d1d1;
            cursor: not-allowed;
          }
        }
        .attrActive{
          background: #ffb800;border-radius: 10px
        }
      }
    }
    .attributeDetail{
      border-top: 1px #d1d1d1 solid;margin-top: 5px;overflow: auto;
    }
  }
}
/* 文字按钮 */
.btn {
  font-size: 12px;
  background: #fff;
  padding: 1px 10px;
  border: 1px #d1d1d1 solid;
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.1);
  margin-top: 5px;
  cursor: pointer;
  outline: none;
  margin-right: 5px;
  margin-bottom: 5px;
}
/* 文字按钮禁用 */
.btnDisable{
  color:#d1d1d1;
  border: 1px #d1d1d1 solid;
  cursor: not-allowed;
}
